	import {
		mapState,
		mapMutations
	} from 'vuex';
	import mInput from '../../../components/m-input.vue';

	export default {
		components: {
			mInput
		},
		data() {
			return {
                loading: false,
				providerList: [],
				hasProvider: false,
				account: '',
				password: '',
				positionTop: 0,
				msg: ''
			}
		},
		computed: mapState(['forcedLogin']),
		methods: {
			/**
			 * 第三方授权初始化
			 */
			// initProvider() {
			// 	const filters = ['weixin', 'qq', 'sinaweibo'];
			// 	uni.getProvider({
			// 		service: 'oauth',
			// 		success: (res) => {
			// 			if (res.provider && res.provider.length) {
			// 				for (let i = 0; i < res.provider.length; i++) {
			// 					if (~filters.indexOf(res.provider[i])) {
			// 						this.providerList.push({
			// 							value: res.provider[i],
			// 							image: '../../../static/img/' + res.provider[i] + '.png'
			// 						});
			// 					}
			// 				}
			// 				this.hasProvider = true;
			// 			}
			// 		},
			// 		fail: (err) => {
			// 			console.error('获取服务供应商失败：' + JSON.stringify(err));
			// 		}
			// 	});
			// },
			initPosition() {
				/**
				 * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
				 * 反向使用 top 进行定位，可以避免此问题。
				 */
				this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
			},
			bindLogin() {
				var regNumber = /\d+/; //验证0-9的任意数字最少出现1次。
				var regString = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,}$/;//验证数字、字母至少出现一次，且只能为数字和字母的组合。
				// var pages = getCurrentPages();
				// var currPage = pages[pages.length - 1]; //当前页面
				// var prevPage = pages[pages.length - 2]; //上一个页面
				/**
				 * 客户端对账号信息进行一些必要的校验。
				 */
				if (this.account.length != 11) {
                    this.loading = false;
					uni.showToast({
						icon: 'none',
						title: '请检查手机号是否正确'
					});
					return;
				}
				if (!(/^1(3|4|5|7|8)\d{9}$/.test(this.account))) {
                    this.loading = false;
					uni.showToast({
						icon: 'none',
						title: '手机号只能为11位数字'
					});
					return;
				}
				if (this.password.length < 8) {
                    this.loading = false;
					uni.showToast({
						icon: 'none',
						title: '密码最短为 8 个字符'
					});
					return;
				}
				if (!(regString.test(this.password))) {
                    this.loading = false;
					uni.showToast({
						icon: 'none',
						title: '密码必须为字母和数字的组合'
					});
					return;
				}
				/**
				 * 下面简单模拟下服务端的处理
				 * 检测用户账号密码是否在已注册的用户列表中
				 * 实际开发中，使用 uni.request 将账号信息发送至服务端，客户端在回调函数中获取结果信息。
				 */
				const loginData = {
					account: this.account,
					password: this.password
				};
				uni.request({
					url: 'http://24l687f160.qicp.vip:43882/login', //仅为示例，并非真实接口地址。
					method: 'GET',
					data: {
						phone: loginData.account,
						password: loginData.password
					},
					success: (res) => {
						if (res.data.code === 0) {
							uni.showToast({
								icon: 'none',
								title: '登陆成功',
							});
							this.login(res.data);
							uni.navigateBack();
						} else {
							uni.showModal({
								content: "用户名密码错误！",
								showCancel: false
							});
						}
					},
					fail: (res) => {
						uni.showModal({
						    content: "请求失败，请重试！",
						    showCancel: false
						})
					}
				});
			},
			// oauth(value) {
			// 	uni.login({
			// 		provider: value,
			// 		success: (res) => {
