			// 				provider: value,
			// 				success: (infoRes) => {
			// 					/**
			// 					 * 实际开发中，获取用户信息后，需要将信息上报至服务端。
			// 					 * 服务端可以用 userInfo.openId 作为用户的唯一标识新增或绑定用户信息。
			// 					 */
			// 					this.toMain(infoRes.userInfo.nickName);
			// 				}
			// 			});
			// 		},
			// 		fail: (err) => {
			// 			console.error('授权登录失败：' + JSON.stringify(err));
			// 		}
			// 	});
			// }, 
			toMain(userName) {
				this.login(userName);
				/**
				 * 强制登录时使用reLaunch方式跳转过来
				 * 返回首页也使用reLaunch方式
				 */
				if (this.forcedLogin) {
					uni.reLaunch({
						url: '../ucenter',
					});
				} else {
					uni.navigateBack();
				}

			},
			...mapMutations(['login'])
		},
		onReady() {
			this.initPosition();
			// this.initProvider();
		}
	}
